@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/bg.d533797c.jpeg) no-repeat center center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {

}

.App {
  text-align: center;
  background-color: rgba(32, 35, 41, 0.7);
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.img{

}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {

}
.author{
  position: fixed;
  text-align: center;
  bottom: 100px;
  width: 100%;
  font-size: 20px;
  font-family: 'PT Sans', sans-serif;
  font-style: italic;

}
.landing{
  display: contents;
}

.landing-button{
  transition-duration: 0.5s;
  background: rgba(0,0,0,0.5);
  color: white;
  border-radius: 250px;
  width: 18%;
  margin-left: auto;
  margin-right: auto;
  position: -webkit-sticky;
  position: sticky;
  font-weight: 700;
  font-family: 'PT Sans', sans-serif;
  font-style: normal;
  top: 45%;
}
.landing-button:hover{
  transition-duration: 0.5s;
  width: 19%;
  font-weight: 700;
  font-style: normal;

  box-shadow: black 0px 0px 15px 5px;
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.TopList-enter{
    opacity: 0.01;
}

.TopList-enter.TopList-enter-active{
    opacity: 1;
    transition: opacity 300ms ease-in;
}
.TopList-leave{
    opacity: 0.01;
}

.TopList-leave.TopList-leave-active{
    opacity: 1;
    transition: opacity 300ms ease-in;
}


.Table{
    width: 50%;
    font-family: 'PT Sans', sans-serif;
    margin: auto;
}
.MuiTableCell-body{
    color : white!important;
}
.MuiCheckbox-colorSecondary.Mui-checked {
    color: #f50057!important;
}
.MuiCheckbox-root{
    color: #cbcbcb!important;
}
button:hover{
    cursor: pointer;
    border: 1px solid #d7d7d7;
}


button{
    border: 1px solid #797979;
    margin-top: 10px;
    box-shadow: 0px 1px 7px 0px black;
}
.elem:hover{
    cursor: pointer;
    border: 1px solid #000000;
    background-color: #000000;
}
.elem {
    border: 1px solid #2f2f2f;
    background-color: #2b2b2b;
    margin-top: 10px;
    box-shadow: 0px 1px 7px 0px black;
    width: 25%;
    margin-left: auto;
    margin-right: auto;
}
.sweet-loading{
    margin-top: 20%;
    display: inline-table;
}

.pagefade-enter, .pagefade-appear { opacity:0; }
.pagefade-enter.pagefade-enter-active,
.pagefade-appear.pagefade-appear-active { opacity:1; transition:opacity 1000ms; }
.pagefade-enter.pagefade-enter-active { transition-delay:1000ms; }
.pagefade-exit { opacity:1; }
.pagefade-exit.pagefade-exit-active { opacity:0; transition:opacity 1000ms; }


.scale-in-center {
    -webkit-animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.puff-in-center{-webkit-animation:puff-in-center .5s cubic-bezier(.215,.61,.355,1.000) both;animation:puff-in-center .5s cubic-bezier(.215,.61,.355,1.000) both}
.fade-in{-webkit-animation:fade-in 1.5s cubic-bezier(.39,.575,.565,1.000) both;animation:fade-in 1.5s cubic-bezier(.39,.575,.565,1.000) both}
@-webkit-keyframes scale-in-center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}
@keyframes scale-in-center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}
@-webkit-keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}
@keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}
@-webkit-keyframes fade-in{0%{opacity:0}100%{opacity:1}}
@keyframes fade-in{0%{opacity:0}100%{opacity:1}}
